<script>
import TableComponent from "@/components/Table/Table.vue";
import PaginationComponent from "@/components/Pagination1/Pagination1.vue";

const toLower = (text) => {
  return text.toString().toLowerCase();
};
// 條件搜尋
const searchByName = (items, term) => {
  if (term) {
    return items.filter(
      (item) =>
        toLower(item.index).includes(toLower(term)) ||
        toLower(item.projectInfo).includes(toLower(term)) ||
        toLower(item.createDate).includes(toLower(term)) ||
        toLower(item.workSpace).includes(toLower(term)) ||
        toLower(item.act_Desc).includes(toLower(term))
    );
  }
  return items;
};

export default {
  name: "signedDocuments-component",
  components: {
    TableComponent,
    PaginationComponent,
  },
  data() {
    return {
      search: "",
      loading: true,
      data: [],
      tableData: [],
      tableList: [],
      tableColumns: [
        {
          id: "index",
          label: "項次",
          width: "70",
        },
        {
          id: "projectInfo",
          label: "緊急搶修單編號(緊急搶修名稱)",
          width: "300",
        },
        {
          id: "createDate",
          label: "建立日期 (開始 ~ 結束日期)",
          width: "300",
        },
        {
          id: "workSpace",
          label: "工作區域",
          width: "300",
        },
        {
          id: "act_Desc",
          label: "簽核節點",
          width: "150",
        },
      ],
    };
  },
  computed: {},
  methods: {
    async getViewSignOffList() {
      try {
        const res = await this.$store.dispatch("getViewSignOffList");
        this.data = JSON.parse(JSON.stringify(res.data.data));
        this.data.forEach((e, index) => {
          e.index = index + 1;
          e.projectInfo = `${e.project_No}(${e.project_Name})`;
          e.createDate = `${e.create_Date}(${e.begin_Date}~${e.end_Date})`;
          e.workSpace = `${e.ld1}/${e.ld2}/${e.ld3}/${e.ld4}`;
        });
        this.tableData = this.data;
        this.tableList = this.tableData.slice(0, 10);
      } catch (err) {
        console.log(err);
      }
    },
    async getViewSignOffDetail(prjNo) {
      try {
        const res = await this.$store.dispatch("getViewSignOffDetail", prjNo);
        const data = res.data.data;
        const { href } = this.$router.resolve({
          path: "/emergencyRepairOrderViewSighOffDetails",
          query: {
            data: JSON.stringify(data),
          },
        });
        window.open(href, "_blank");
      } catch (err) {
        console.log(err);
      }
    },
    openDetails(prjNo) {
      this.getViewSignOffDetail(prjNo);
    },
    changePage(page) {
      const cur = (page - 1) * 10;
      const last = page * 10;
      this.tableList = this.tableData.slice(cur, last);
      this.$store.state.currentPage = page;
    },
    searchHandler(evt) {
      if (evt.code === "Enter" || evt.code === "NumpadEnter") {
        const val = evt.target.value;
        this.tableData = searchByName(this.data, val);
        this.tableList = this.tableData.slice(0, 10);
        this.$store.state.currentPage = 1;
      }
    },
  },
  async mounted() {
    this.getViewSignOffList();
    this.$store.state.currentPage = 1;
    this.$store.commit("HEADER_HANDLER", true);
    this.$store.commit("BREADCRUMB_HANDLER", true);
    this.$root.$children[0].sidebarSwitch = true;
  },
};
</script>

<template src="./signedDocuments.html"></template>
<style lang="sass" scoped src="../../../../public/styles/custom.sass"></style>
